<template>
  <div class="grabar">
      <button class="boton-redondo" @click="grabar">
             <div class="letra">GRABAR</div> 
      </button>
  </div>
</template>

<script>
import Swal from "sweetalert2"
export default {
     methods: {
    grabar(){
    Swal.fire({
                title: '¿Quieres prograr la grabacion de la camara?',
                text: "Si no quieres, la grabacion comenzara al pulsar no",
                showCancelButton: true,
                confirmButtonText: 'Si',
                cancelButtonText: 'No',
                showLoaderOnConfirm: true
            }).then((result) => {
                if (result.isConfirmed) {
                    Swal.fire({
                        title: 'Introduce las horas de la programación',
                        text:"Recuerda que el tiempo maximo de grabacion son 8 horas",
                        // input: 'text',
                        // inputAttributes: {
                        //     autocapitalize: 'off'
                        // },
                        html:'<input type="time"/>',
                        showCancelButton: true,
                        confirmButtonText: 'Enviar',
                        showLoaderOnConfirm: true
                    })
                }
            })
    }
  }
}
</script>

<style scoped>

.grabar{
    position: fixed;
    bottom: 5px;
    right: 5px;
}

.boton-redondo{
    background: red;
    border-radius: 50%;
    height: 120px;
    width: 120px;
    border: black 2px solid;
}

.intermedio{
    background: red;
    border-radius: 50%;
    height: 80px;
    width: 80px; 
    margin-left: auto;
    margin-right: auto;
    border: black 2px solid;
    color: white;
    font-weight: bolder;
    font-size: 1.5em;
    text-align: center;
}

.letra{
    color: white;
    font-weight: bold;
    /* margin-top: 20px;
    margin-bottom: 20px; */
    line-height: 80px;
}
</style>