<template>
	<div>
		<Menu :titulo="titulo" />
		<div @click="cambiarDisposicion">
			<font-awesome-icon icon="th"  style="cursor:pointer; margin-left: 5%; font-size: 1.5em;"/>
			<div style="display:inline;height:40px;line-height:40px">Cambiar disposicion</div>
		</div>
		<CamarasRT1 v-if='disposicion==1'/>
		<CamarasRT2 v-else/>

		<Aviso/>
		<Grabar/>
	</div>
</template>

<script>
import Menu from "@/components/Menu";
import Aviso from "@/components/Aviso";
import Grabar from "@/components/BotonGrabar";
import CamarasRT1 from "@/components/CamarasRT1";
import CamarasRT2 from "@/components/CamarasRT2";

export default {
	name: "TiempoReal",
	components: {
		Menu,
		CamarasRT1,
		CamarasRT2,
		Aviso,
		Grabar
	},
	data() {
		return {
		titulo: "TIEMPO REAL",
		disposicion: 1
		};
	},
	methods: {
		/**
		 * Cambiar la disposicion de las camaras
		 */
		cambiarDisposicion(){
			if(this.disposicion == 1){
				this.disposicion = 2;
			}
			else {
				this.disposicion = 1;
			}
		}
	}
};
</script>

