<script>
import Swal from "sweetalert2"
export default {
  props: {
    nombre:String,
    pos: Number,
    imagen: String,
    estado: Boolean,
  },
  data() {
    return {
      switch1: this.estado,
    }
  },
  methods: {
    grabar(){
    Swal.fire({
                title: '¿Quieres prograr la grabacion de la camara?',
                text: "Si no quieres, la grabacion comenzara al pulsar no",
                showCancelButton: true,
                confirmButtonText: 'Si',
                cancelButtonText: 'No',
                showLoaderOnConfirm: true
            }).then((result) => {
                if (result.isConfirmed) {
                    Swal.fire({
                        title: 'Introduce las horas de la programación',
                        text:"Recuerda que el tiempo maximo de grabacion son 8 horas",
                        // input: 'text',
                        // inputAttributes: {
                        //     autocapitalize: 'off'
                        // },
                        html:'<input type="time"/>',
                        showCancelButton: true,
                        confirmButtonText: 'Enviar',
                        showLoaderOnConfirm: true
                    })
                }
            })
    }
  }
};
